body {
  margin: 0;
  font-family: -apple-system, 'Roboto Mono', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #b23148;
  color: white;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* css order:
1. outer sizing and attributes
2. inner content 

go out-to-in
*/

.card {
  /* display: flex;
  flex-wrap: wrap; */
  position: relative;
  border: 3px solid #282828;
  background-color: #a2a2a8;
  color: #242424;
  margin: 15vh auto 0;
  width: 300px;
  height: 360px;
  box-shadow: 1px 2px 8px -2px rgba(0,0,0,0.76);
  -webkit-box-shadow: 1px 2px 8px -2px rgba(0,0,0,0.76);
  -moz-box-shadow: 1px 2px 8px -2px rgba(0,0,0,0.76);
}

.btn {
  user-select: none;
}

.btn:active {
  transform: translateY(2px)
}

#start_stop {
  margin: 30px auto 12px;
  width: 40%;
  background-color: #DDD;
  border: 2px solid #242424;
  padding: 5px 0 5px 2px;
  font-size: 2em;
}

#reset {
  width: 20%;
  margin: 0 auto;
  border: 2px solid #242424;
  background-color: #999;
  font-size: 1.25em;
}

#middle {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.top-mid-label {
  margin: 15px 0 5px;
  width: 45%;
}

.adj-btn {
  width: 15%;
  border: 1px solid #242424;
  border-radius: 10px;
}

#timer-label {
  position: absolute;
  width: 70%;
  bottom: 55px;
  right: 0px;
  font-size: 1.25em;
}

#time-box {
  position: absolute;
  width: 70%;
  padding: 5px 0;
  bottom: 0px;
  right: 0px;
  background-color: #4a4a4a;
  border-top: 3px solid #282828;
  border-left: 3px solid #282828;
}

#time-left {
  color: orangered;
  font-size: 2em;
}

#footer {
  margin: 4px auto;
  width: 280px;
  color: white;
  font-size: 0.8em;
  text-align: right;
  text-decoration: none;
}

a {
  color: white;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a:active {
  text-decoration: underline;
}